	module.exports = {"pathContext":{"docHtml":"<h1>fs.realpath</h1>\n<p>A backwards-compatible fs.realpath for Node v6 and above</p>\n<p>In Node v6, the JavaScript implementation of fs.realpath was replaced\nwith a faster (but less resilient) native implementation.  That raises\nnew and platform-specific errors and cannot handle long or excessively\nsymlink-looping paths.</p>\n<p>This module handles those cases by detecting the new errors and\nfalling back to the JavaScript implementation.  On versions of Node\nprior to v6, it has no effect.</p>\n<h2>USAGE</h2>\n<pre><code class=\"language-js\">var rp = require('fs.realpath')\n\n// async version\nrp.realpath(someLongAndLoopingPath, function (er, real) {\n  // the ELOOP was handled, but it was a bit slower\n})\n\n// sync version\nvar real = rp.realpathSync(someLongAndLoopingPath)\n\n// monkeypatch at your own risk!\n// This replaces the fs.realpath/fs.realpathSync builtins\nrp.monkeypatch()\n\n// un-do the monkeypatching\nrp.unmonkeypatch()\n</code></pre>","docPath":"argo-ci/node_modules/fsevents/node_modules/fs.realpath/readme","proj":"argo-ci"}}

